'use strict';

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import isPlainObject from '@stdlib/assert-is-plain-object';
import { isPrimitive as isBoolean } from '@stdlib/assert-is-boolean';
import { isPrimitive as isInteger } from '@stdlib/assert-is-integer';
import isndarrayLike from '@stdlib/assert-is-ndarray-like';
import isReadOnly from '@stdlib/ndarray-base-assert-is-read-only';
import hasOwnProp from '@stdlib/assert-has-own-property';
import iteratorSymbol from '@stdlib/symbol-iterator';
import nulls from '@stdlib/array-base-nulls';
import getShape from '@stdlib/ndarray-shape';
import normalizeIndex from '@stdlib/ndarray-base-normalize-index';
import numel from '@stdlib/ndarray-base-numel';
import ndslice from '@stdlib/ndarray-base-slice';
import Slice from '@stdlib/slice-ctor';
import args2multislice from '@stdlib/slice-base-args2multislice';
import format from '@stdlib/error-tools-fmtprodmsg';


// MAIN //

/**
* Returns an iterator which iterates over each view along a specified dimension.
*
* @param {ndarray} x - input value
* @param {integer} dim - dimension index
* @param {Options} [options] - function options
* @param {boolean} [options.readonly=true] - boolean indicating whether returned views should be read-only
* @param {boolean} [options.keepdim=false] - boolean indicating whether returned views should include the selected dimension as a singleton dimension
* @throws {TypeError} first argument must be an ndarray
* @throws {TypeError} second argument must be an integer
* @throws {RangeError} dimension index exceeds the number of dimensions
* @throws {TypeError} options argument must be an object
* @throws {TypeError} must provide valid options
* @throws {Error} cannot write to a read-only array
* @returns {Iterator} iterator
*
* @example
* import array from '@stdlib/ndarray-array';
* import ndarray2array from '@stdlib/ndarray-to-array';
*
* var x = array( [ [ [ 1, 2 ], [ 3, 4 ] ], [ [ 5, 6 ], [ 7, 8 ] ] ] );
* // returns <ndarray>
*
* var iter = nditerSelectDimension( x, 0 );
*
* var v = iter.next().value;
* // returns <ndarray>
*
* var arr = ndarray2array( v );
* // returns [ [ 1, 2 ], [ 3, 4 ] ]
*
* v = iter.next().value;
* // returns <ndarray>
*
* arr = ndarray2array( v );
* // returns [ [ 5, 6 ], [ 7, 8 ] ]
*
* // ...
*/
function nditerSelectDimension( x, dim ) {
	var options;
	var shape;
	var ndims;
	var opts;
	var iter;
	var FLG;
	var idx;
	var N;
	var d;
	var i;

	if ( !isndarrayLike( x ) ) {
		throw new TypeError( format( 'null4f', x ) );
	}
	if ( !isInteger( dim ) ) {
		throw new TypeError( format( 'null7f', dim ) );
	}
	opts = {
		'writable': false,
		'keepdim': false
	};
	if ( arguments.length > 2 ) {
		options = arguments[ 2 ];
		if ( !isPlainObject( options ) ) {
			throw new TypeError( format( 'null2V', options ) );
		}
		if ( hasOwnProp( options, 'readonly' ) ) {
			if ( !isBoolean( options.readonly ) ) {
				throw new TypeError( format( 'null2o', 'readonly', options.readonly ) );
			}
			opts.writable = !options.readonly;
			if ( opts.writable && isReadOnly( x ) ) {
				throw new Error( format( 'nullF4' ) );
			}
		}
		if ( hasOwnProp( options, 'keepdim' ) ) {
			if ( !isBoolean( options.keepdim ) ) {
				throw new TypeError( format( 'null2o', 'keepdim', options.keepdim ) );
			}
			opts.keepdim = options.keepdim;
		}
	}
	// Retrieve input array meta data:
	shape = getShape( x );
	ndims = shape.length;

	// Normalize the dimension index:
	d = normalizeIndex( dim, ndims-1 );
	if ( d === -1 ) {
		throw new RangeError( format( 'nullF8', ndims, dim ) );
	}
	// Check whether the input array is empty...
	N = numel( shape );
	if ( N === 0 ) {
		FLG = true;
	}
	// Compute the number of views (i.e., the dimension size):
	N = shape[ d ];

	// Initialize a counter:
	i = -1;

	// Initialize an index array for generating slices:
	idx = nulls( ndims );

	// Create an iterator protocol-compliant object:
	iter = {};
	setReadOnly( iter, 'next', next );
	setReadOnly( iter, 'return', end );

	// If an environment supports `Symbol.iterator`, make the iterator iterable:
	if ( iteratorSymbol ) {
		setReadOnly( iter, iteratorSymbol, factory );
	}
	return iter;

	/**
	* Returns an iterator protocol-compliant object containing the next iterated value.
	*
	* @private
	* @returns {Object} iterator protocol-compliant object
	*/
	function next() {
		var s;

		i += 1;
		if ( FLG || i >= N ) {
			return {
				'done': true
			};
		}
		// Update the index array to generate the next view:
		if ( opts.keepdim ) {
			idx[ d ] = new Slice( i, i+1, 1 );
		} else {
			idx[ d ] = i;
		}
		// Create a multi-slice for the next view:
		s = args2multislice( idx );

		// Return the next slice:
		return {
			'value': ndslice( x, s, true, opts.writable ),
			'done': false
		};
	}

	/**
	* Finishes an iterator.
	*
	* @private
	* @param {*} [value] - value to return
	* @returns {Object} iterator protocol-compliant object
	*/
	function end( value ) {
		FLG = true;
		if ( arguments.length ) {
			return {
				'value': value,
				'done': true
			};
		}
		return {
			'done': true
		};
	}

	/**
	* Returns a new iterator.
	*
	* @private
	* @returns {Iterator} iterator
	*/
	function factory() {
		return nditerSelectDimension( x, dim, opts );
	}
}


// EXPORTS //

export default nditerSelectDimension;
